/*------------------------------------*\
  ASSETS AND BUILDUP
\*------------------------------------*/

@import 'poly-fluid-sizing';
@import 'include-media';
@import '../node_modules/latte-carousel/dist/latte-carousel.min.css';

@font-face {
    font-family: 'Hershey-Noailles-Futura';
    src: url('../fonts/Hershey-WS-AC-Regular.woff2') format('woff2'),
        url('../fonts/Hershey-Noailles-Futura-Simplex-Regular.woff') format('woff');
    font-style: normal;
    font-weight: 500;
}

