    font-style: italic;
    font-weight: 500;
}

@font-face {
    font-family: 'Hershey-Noailles-Futura';
    src: url('../fonts/Hershey-WS-AC-Bold.woff2') format('woff2'),
        url('../fonts/Hershey-Noailles-Futura-Simplex-Bold.woff') format('woff');
    font-style: normal;
    font-weight: 800;
}

@font-face {
    font-family: 'Hershey-Noailles-Futura';
    src: url('../fonts/Hershey-WS-AC-BoldItalic.woff2') format('woff2'),
        url('../fonts/Hershey-WS-AC-BoldItalic.woff') format('woff');
    font-style: italic;
    font-weight: 800;
}

@font-face {
    font-family: 'Hershey-Noailles-Futura';
    src: url('../fonts/Hershey-Noailles-Futura-Simplex-Light.woff2') format('woff2'),
        url('../fonts/Hershey-Noailles-Futura-Simplex-Light.woff') format('woff');
