/*------------------------------------*\
  VARIABLES
\*------------------------------------*/

$breakpoints: (phone: 320px, tablet: 700px, desktop: 1024px);

$maincolumn: 600px;

$gray: rgb(196, 196, 196);

$txtxs:0.58rem;
$txts:0.8rem;
$txtm:1.5rem;
$txtl:3rem;
$lineheight:0.96em;

@mixin textsize($fs) {
    font-size: $fs;
    line-height: $lineheight;
}

$strokewidth:0.05rem;
$timing: 300ms ease;

/*------------------------------------*\
  BASE
\*------------------------------------*/

html {
    @include poly-fluid-sizing('font-size',
        (320px: 20px, 600px:20px, 1024px:24px, 1400px:24px, 1920px:24px));
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
}

html,
body {
    margin: 1rem;
    font-family: 'Hershey-Noailles-Futura', sans-serif;
    font-weight: normal;
    line-height: $lineheight;

    @include media("<tablet") {
        margin: 0.4rem;
    }
}

svg,
svg path {
    stroke: none;
    fill: none;
    overflow: visible;
}

svg.fill path {
    fill: black;
}
svg.fill-white path {
    fill: white;
}
svg.fill-trans path {
    fill: rgba(255,255,255,0.001);
}

svg.stroke path {
    stroke: black;
    stroke-width: $strokewidth;
}

div.stroke-hover {
    transition: color $timing;
}

div.stroke-hover svg path {
    transition: fill $timing;
}

div.stroke-hover:hover path {
    fill: white;
}

div.stroke-hover-gray:hover path {
    fill: $gray;
}
div.stroke-hover-black:hover path {
    fill: black;
}
div.stroke-hover-trans:hover path {
    fill: rgba(255,255,255,0.001);
}

div.stroke-hover:hover {
    color: black;
}

div {
    box-sizing: border-box;
}

a{
    color:black;
}


h1 {
    font-size: 4rem;
    font-weight: inherit;
    margin: 0;
    padding-bottom:0.1em;

    @include media("<tablet") {
        font-size: 2.8rem;
    }

}

h2 {
    font-size: 3rem;
    font-weight: inherit;
}

h3 {
    font-size: 2rem;
    font-weight: inherit;
}

h4 {
    font-size: 1.5rem;
    line-height: 0.88em;
    margin: 0;
    font-weight: inherit;
}

img {
    pointer-events: none;
}

.light {
    font-weight: lighter;
}

.wc-solid {
    font-family: 'Hershey-Noailles-WS-AC';
}

.wc-outline {
    font-family: 'Hershey-Noailles-WS-AC-OUTLINE';
    line-height: 0.75em;
}

.no-p p {
    margin: 0;
}

.p-float p {
    display: inline-block;
}


/*------------------------------------*\
  MAIN
\*------------------------------------*/

.main-content {
    position: relative;
    width: 100%;
    height: auto;
    margin: 0 auto;
    padding-bottom: 4rem;
    @include media("<tablet") {
        padding-bottom:0.5rem;
    }
}

.main-column {
    width: $maincolumn;
    margin: 0 auto;

    @include media("<tablet") {
        width: 100%;
    }
}

.text-correction p {
    // width:585px;
    // padding-left:15px;
}

.dropdown-closed {
    display: none;
    overflow: hidden;
}

.dropdown-open {
    overflow: visible;
}

.dropdown-open p:first-of-type,
.dropdown-closed p:first-of-type {
    margin-top: 0;
}

.dropdown-open p:last-of-type,
.dropdown-closed p:last-of-type {
    margin-bottom: 0;
}


/*------------------------------------*\
  UI ELEMENTS
\*------------------------------------*/

.ui-ellipsis {
    position: relative;
    display: table;
    width: auto;
    height: 1.2rem;
    margin: 1rem auto;
    cursor: pointer;
}

.ui-ellipsis svg {
    height: 1.2rem;
}

.ui-ellipsis path {
    fill: rgba(255, 255, 255, 0.001);
}

.ui-ellipsis-active {
    display: none;
}

.ui-dropdown.dropdown-open .ui-ellipsis-default {
    display: none;
}

.ui-dropdown.dropdown-open .ui-ellipsis-active {
    display: block;
}

.ui-divider {
    position: relative;
    width: 100%;
}

.ui-divider-height-1 {
    height: 1rem;
}

.ui-divider-height-2 {
    height: 2rem;
}

.ui-divider-height-3 {
    height: 3rem;
}

.ui-divider-height-4 {
    height: 4rem;
}

.ui-divider svg {
    width: 100%;
    height: 100%;
}

.ui-divider svg path {
    vector-effect: non-scaling-stroke;
}



/*------------------------------------*\
  HEAD FIXED
\*------------------------------------*/

#introduction-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    min-height: 1542px;
    background-image: url("../images/head_2560.png");
    background-size: 1600px;
    background-position-x: center;
    background-repeat: no-repeat;
    z-index: 0;

    @include media("<tablet") {
        background-size: 200vw;
    }

    @include media(">1600px") {
        background-size: 100vw;
    }
}
#background-priority{
    display:none;
}

#home-button {
    position: fixed;
    width: auto;
    height: auto;
    right: 2rem;
    top: 2rem;

    @include media("<tablet") {
        right: 1rem;
        top: 1rem;
    }

    z-index: 99;
    cursor: pointer;
    transition: transform $timing;
}

#home-button.default {
    transform: scale(0) rotate(5deg);
}

#home-button.scrolled {
    transform: scale(1) rotate(0deg);
}


#home-button:hover .home-text {
    color: black;
}

#home-button .home-bg {
    position: absolute;
    width: 100%;
    height: 100%;
}

#home-button .home-bg svg {
    width: 100%;
    height: 100%;
}

#home-button .home-text {
    position: relative;
    padding: 1rem 1.5rem;
    padding-left: 1.4rem;
    color: white;
}

/*------------------------------------*\
  INTRODUCTION
\*------------------------------------*/

.introduction-title {
    margin-top: 8.5rem;
    @include media(">1600px") {
        margin-top: 12.8vw;
    }
}

.introduction-text-top {
    margin-top: 1.2rem;
}

.introduction-text-dropdown {
    @include textsize($txts);
}

.introduction-text-dropdown h1,
.introduction-text-dropdown h2 {
    font-family: 'Hershey-Noailles-WS-AC';
    margin: 0;
    line-height: 0.48em;
}

.introduction-artists-list {
    position: relative;
    display: inline-block;
    width: auto;
    height: auto;
    cursor: pointer;
}

.introduction-artists-list-name{
    position: relative;
    margin:0.25rem 0rem;
    z-index: 2;
    margin-left:0.5rem;
}
.introduction-artists-circle {
    position: absolute;
    width:100%;
    width:calc(100% + 0.5rem);
    height:100%;
    top:0;
    left:0rem;
}
.introduction-artists-circle svg{
    width:100%;
    height:100%;
}

/*------------------------------------*\
  ARTISTS & NEWS
\*------------------------------------*/

.artist-wrap,
.news-wrap {
    position: relative;
    padding-bottom: 0.5rem;
}

.news-wrap {
    background-color: white;
    background-color: var(--background-color);
    -webkit-box-shadow: 0 0 2rem 2rem var(--background-color);
    box-shadow: 0 0 2rem 2rem var(--background-color);
}

.artist-status,
.artist-name,
.news-date {
    text-align: center;
    font-weight: bold;
    margin-top: 0.5rem;
}

.artist-status {
    font-weight: lighter;
}

.artist-title,
.news-title {
    @include textsize($txtl);
    line-height: 0.88em;
    text-align: center;
    margin-bottom: 1rem;
}

.artist-image {
    position: relative;
    max-width: 100%;
    height: 420px;
    margin-top: 2rem;
}

