.video-clip{
    height:345px;
    @include media("<tablet") {
        height:54vw;
    }
}

.artist-image img {
    position: absolute;
    display: block;
    width: 100%;
    height:100%;
    object-fit: cover;
    
}

.artist-image-copyright {
    @include textsize($txtxs);
    text-align: center;
}

.artist-text-dropdown {}

.artist-external,
.news-external {
    max-width: 100%;
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.artist-external-shape,
.news-external-shape,
.contact-external-shape {
    position: relative;
    display: inline-block;
    margin: 0 1rem;
    margin-bottom: 1rem;
    color: white;
    margin-left:3.8rem;
}

.artist-external-arrow,
.news-external-arrow,
.contact-external-arrow {
    position: absolute;
    width:2rem;
    height:1.5rem;
    top:50%;
    transform:translateY(-50%);
    margin-left:-2.8rem;
    transition:margin-left 180ms ease;
}
.artist-external-shape:hover .artist-external-arrow,
.news-external-shape:hover .news-external-arrow,
.contact-external-shape:hover .contact-external-arrow{
    margin-left:-2.4rem;
}

.artist-external-bg,
.news-external-bg {
    position: absolute;
    width: 100%;
    height: 100%;
}

.artist-external-bg svg,
.news-external-bg svg {
    position: relative;
    width: 100%;
    height: 100%;
    vector-effect: non-scaling-stroke;
    overflow: visible;
}

.artist-external-bg svg path,
.news-external-bg svg path {
    vector-effect: non-scaling-stroke;
}

.artist-external-text,
.news-external-text {
    position: relative;
    padding: 1.2rem 2.5rem;
    text-transform: uppercase;
}


.shape-0 {
    margin-left: 0.5rem;
}

.shape-2 {
    margin:0.3rem 0;
    margin-left: 1.2rem;
    margin-right: 1.6rem;
}
.shape-6{
    margin-right:0.5rem;
    margin-left:0.5rem;
    margin-top:0.4rem;
    margin-bottom:0.4rem;
}
.shape-4{
    padding-left:1.9rem;
}
.shape-9,
.shape-11{
    margin:0.4rem 0;
}
.shape-10{
    margin:0.6rem 0;
}

/*------------------------------------*\
  SOCIAL & CONTACT
\*------------------------------------*/

.social-wrap-external,
.contact-wrap {
    position: relative;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-evenly;
    align-items: center;
}

.social-entry-external,
.contact-entry {
    position: relative;
    width: 3rem;
    height: 3rem;
    margin:0.5rem 0;
}

.social-wrap-external a,
.contact-entry a {
    margin: 0.5rem 2rem;
}
.social-entry-external path {
    vector-effect: non-scaling-stroke;
}
.social-entry-external svg{
    height:3rem;
    margin:0 auto;
}

.contact-external {
    max-width: 100%;
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    margin: 0.5rem 0;
    flex-wrap: wrap;
}

.contact-external-shape {
    position: relative;
    display: inline-block;
    margin: 0 1rem;
    color: white;
    margin-bottom: 1rem;
    margin-left:3.8rem;
}

.contact-external-bg {
    position: absolute;
    width: 100%;
    height: 100%;
}

.contact-external-bg svg {
    position: relative;
    width: 100%;
    height: 100%;
    vector-effect: non-scaling-stroke;
    overflow: visible;
}

.contact-external-bg svg path {
    vector-effect: non-scaling-stroke;
}

.contact-external-text {
    position: relative;
    padding: 1rem 1.5rem;
    text-transform: uppercase;
}

.banner-wrap {
    position: relative;
    max-width: 600px;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-evenly;
    align-items: center;
    column-gap: 2rem;
    margin-top:0.5rem;
    @include media("<=800px") {
        max-width: 100%;
    }
}

.banner-wrap img { 
    width:auto;
    max-width:100%;
}

/*------------------------------------*\
  CYCLE
\*------------------------------------*/

.cycle-wrap{
    position: relative;
    margin: 1rem auto;
    transition:height 300ms ease;
}

.cycle-images{
    position: relative;
}
.cycle-video p{
    display:none;
}
.cycle-video div:first-of-type{
    padding:420px 0 0 0!important;
}

.cycle-entry {
    position: relative;
    margin: 0rem auto;
    display: block;
    @include media("<=800px") {
        width: 80vw;
    }
}

.latte-previous,
.latte-next {
    display:none;
    border: none;
    width: 3rem;
    height: 3rem;
    transform:translateX(-4rem) translateY(-50%);
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url(../svg/WC-ACElement_arrowleft.svg);
}

.latte-next {
    transform:translateX(1rem) translateY(-50%);
    background-image: url(../svg/WC-ACElement_arrowright.svg);
}

.cycle-next,
.cycle-previous{
    position: absolute;
    display: block;
    left:50%;
    margin-left:-300px;

    @include media("<=800px") {
        width: 4vw;
        height: 50%;
        left:0;
        margin-left:0;
        transform:translateX(-0.2rem) translateY(-50%);
    }

}
.cycle-next{
    margin-left:300px;

    @include media("<=800px") {
        right:0;
        left:auto;
        marign-left:0;
        transform:translateX(0.2rem) translateY(-50%);
    }

}

.cycle-image {
    position: relative;
    max-width: 100%;
    width:100%;
    height: 420px;
    @include media("<tablet") {
        height:80vw;
    }
}

.cycle-image img {
    width:100%;
    height:100%;
    object-fit: cover;
    background-color:rgb(232,232,252);
}

.cycle-description {
    text-align: center;
    margin:1em auto;
    min-height: 1rem;
    height:auto;
    padding:0 2em;
    opacity: 1;
    transition: opacity 150ms ease,
                height 300ms ease;
    @include textsize($txtxs);
}
.cycle-move{
    opacity: 0;
}